// !process.env.SKIP_ENV_VALIDATION && (await import("./env.mjs"));
import { createStore } from "zustand";
import { type appModes, type AppProps, type AppActions, type RecursivePartial, EditModes } from "./types";
import { createContext } from "react";

const initialState: RecursivePartial<AppProps> = {
  APP: {
    NAME: "App Name",
    EDIT_MODE: EditModes.drawer,
  },
};
// const stateOverrideApp: RecursivePartial<AppProps['APP']> = {}

export type AppStore = ReturnType<typeof createAppStore>;

export type AppStoreProps = AppProps & AppActions;

export const createAppStore = (initProps?: Partial<AppProps>) => {
  return createStore<AppStoreProps>()((set) => ({
    ...initialState,
    APP: {
      MODE: (process.env.NEXT_PUBLIC_APP_MODE as appModes) || "",
      BRAND: process.env.NEXT_PUBLIC_APP_BRAND || "",
      URL: process.env.NEXT_PUBLIC_APP_URL || "",
      RECAPTCHA_SITE_KEY: process.env.NEXT_PUBLIC_RECAPTCHA_SITE_KEY || "",
      SPEED_DIAL: false,
      DARK_MODE: false,
      // setDarkMode: (value: boolean) => set((state) => ({ APP: { ...state.APP, DARK_MODE: value } })),
      // use get to access the current state
      setDarkMode: (value: boolean) =>
        set((state) => ({ APP: { ...state.APP, DARK_MODE: value } })),
      HEADER_FIXED: true,
      setHeaderFixed: (value: boolean) =>
        set((state) => ({ APP: { ...state.APP, HEADER_FIXED: value } })),
      // @ts-ignore
      MAIN_MENU: {
        open: false,
        setOpen: (value: boolean) =>
          set((state) => ({
            APP: { ...state.APP, MAIN_MENU: { ...state.APP.MAIN_MENU, open: value } },
          })),
      },
      NAME: "App Name",
      APP: "APP",
      TAG_LINE: "Tag Line",
      ALT_TAGLINE: "Alt Tag Line",
      TITLE: "Title",
      DESC: "Description",
      KEYWORDS: "Keywords",
      LOGO: "/images/logo.png",
      PHONE: "+33 2 00 00 00 00",
      EMAIL: "support@example.com",
      THEME: {
        COLORS: {
          PRIMARY: "#000000",
          BG: "#ffffff",
        },
        LOGIN_BG: "bg-slate-200",
      },
      ...initProps?.APP,
      // ...stateOverrideApp
    },
    SEARCH: {
      query: "",
      setQuery: (value: string) => set((state) => ({ SEARCH: { ...state.SEARCH, query: value } })),
      results: [],
      setResults: (value: any[]) =>
        set((state) => ({ SEARCH: { ...state.SEARCH, results: value } })),
      selected: {},
      setSelected: (value: any) =>
        set((state) => ({ SEARCH: { ...state.SEARCH, selected: value } })),
      view: "list",
      setView: (value: string) => set((state) => ({ SEARCH: { ...state.SEARCH, view: value } })),
      FILTERS: {
        open: false,
        setOpen: (value: boolean) =>
          set((state) => ({
            SEARCH: { ...state.SEARCH, FILTERS: { ...state.SEARCH.FILTERS, open: value } },
          })),
      },
    },
    MODALS: {
      CHANGE_PASSWORD: {
        open: false,
        closeModal: () =>
          set((state) => ({
            MODALS: {
              ...state.MODALS,
              CHANGE_PASSWORD: { ...state.MODALS.CHANGE_PASSWORD, open: false },
            },
          })),
        openModal: () =>
          set((state) => ({
            MODALS: {
              ...state.MODALS,
              CHANGE_PASSWORD: { ...state.MODALS.CHANGE_PASSWORD, open: true },
            },
          })),
        user: {
          id: "",
          email: "",
          name: "",
        },
        setUser: (value: any) =>
          set((state) => ({
            MODALS: {
              ...state.MODALS,
              CHANGE_PASSWORD: { ...state.MODALS.CHANGE_PASSWORD, user: value },
            },
          })),
      },
      TIERS: {
        open: false,
        closeModal: () =>
          set((state) => ({
            MODALS: { ...state.MODALS, TIERS: { ...state.MODALS.TIERS, open: false } },
          })),
        openModal: () =>
          set((state) => ({
            MODALS: { ...state.MODALS, TIERS: { ...state.MODALS.TIERS, open: true } },
          })),
        data: {},
      },
      TIERS_CREATE: {
        open: false,
        closeModal: () =>
          set((state) => ({
            MODALS: {
              ...state.MODALS,
              TIERS_CREATE: { ...state.MODALS.TIERS_CREATE, open: false },
            },
          })),
        openModal: () =>
          set((state) => {
            console.log("state.MODALS.TIERS_CREATE", state.MODALS.TIERS_CREATE);
            return {
              MODALS: { ...state.MODALS, TIERS_CREATE: { ...state.MODALS.TIERS_CREATE, open: true } },
            }
          }
          ),
      },
      TIERS_EDIT: {
        open: false,
        closeModal: () =>
          set((state) => ({
            MODALS: { ...state.MODALS, TIERS_EDIT: { ...state.MODALS.TIERS_EDIT, open: false } },
          })),
        openModal: () =>
          set((state) => ({
            MODALS: { ...state.MODALS, TIERS_EDIT: { ...state.MODALS.TIERS_EDIT, open: true } },
          })),
      },
      QR: {
        open: false,
        closeModal: () =>
          set((state) => ({
            MODALS: { ...state.MODALS, QR: { ...state.MODALS.QR, open: false } },
          })),
        openModal: () =>
          set((state) => ({ MODALS: { ...state.MODALS, QR: { ...state.MODALS.QR, open: true } } })),
        setOpen: (value: boolean) =>
          set((state) => ({
            MODALS: { ...state.MODALS, QR: { ...state.MODALS.QR, open: value } },
          })),
        data: {},
        setData: (value: any) =>
          set((state) => ({
            MODALS: { ...state.MODALS, QR: { ...state.MODALS.QR, data: value } },
          })),
      },
      REPORT: {
        open: false,
        closeModal: () =>
          set((state) => ({
            MODALS: { ...state.MODALS, REPORT: { ...state.MODALS.REPORT, open: false } },
          })),
        openModal: () =>
          set((state) => ({
            MODALS: { ...state.MODALS, REPORT: { ...state.MODALS.REPORT, open: true } },
          })),
      },
    },
    HISTORY: {
      push: () => { },
      replace: () => { },
      back: () => { },
      forward: () => { },
    },
    ANNUAIRE: {
      TITLE: process.env.NEXT_PUBLIC_ANNUAIRE_TITLE || "",
      SUB_TITLE: process.env.NEXT_PUBLIC_ANNUAIRE_SUB_TITLE || "",
      BANNER: process.env.NEXT_PUBLIC_ANNUAIRE_BANNER || null,
      SURVEY: process.env.NEXT_PUBLIC_ANNUAIRE_SURVEY || "",
    },
    FORMS: {
      LOGIN: {
        email: "",
        password: "",
      },
      SIGNUP: {
        email: "",
        password: "",
        passwordConfirmation: "",
      },
      WIZARD: {
        step: 0,
        setStep: (value: number) =>
          set((state) => ({
            FORMS: { ...state.FORMS, WIZARD: { ...state.FORMS.WIZARD, step: value } },
          })),
        stepData: {},
        setStepData: (value: any) =>
          set((state) => ({
            FORMS: { ...state.FORMS, WIZARD: { ...state.FORMS.WIZARD, stepData: value } },
          })),
      },
      LINK: {
        id: "",
        url: "",
        advanced: false,
        hasQr: false,
        qrSettings: null,
        qrData: null,
        isSecure: false,
        password: null,
        shortLink: null,
        isFree: false,
      },
      setForm: (form: string, value: any) =>
        set((state) => ({ FORMS: { ...state.FORMS, [form]: value } })),
      setFormValue: (form: string, key: string, value: any) =>
        set((state) => ({
          FORMS: { ...state.FORMS, [form]: { ...state.FORMS[form], [key]: value } },
        })),
    },
    counter: 0,
    increment: () => set((state) => ({ counter: state.counter + 1 })),
    decrement: () => set((state) => ({ counter: state.counter - 1 })),
    setSpeedDial: (value: boolean) =>
      set((state) => ({ APP: { ...state.APP, SPEED_DIAL: value } })),
  }));
};

// export default useStore;
export const AppContext = createContext<AppStore | null>(null);
